.teams__teamInfo {
    overflow: hidden;
    display: flex;
    flex-direction: row;
    line-height: 40px;
    .teams__teamName {
        flex-basis: 30%;
        font-size: 18px;
        @media (min-width: 568px) {
                flex-basis: 20%; }
        @media (min-width: 1280px) {
                flex-basis: 15%; } }
    .teams__gameStatus {
        flex-basis: 18%;
        text-transform: uppercase;
        font-weight: bold;
        letter-spacing: 1px; }
    .teams__objectiveContainer, .objectiveContainer {
        flex-grow: 1;
        display: flex;
        flex-direction: row;
        height: 40px;
        @media (min-width: 568px) {
                flex-grow: 0;
                flex-basis: 40%; }
        .objectiveContainer__item {
            flex-basis: 20%;
            height: 40px;
            display: flex;
            flex-direction: column;
            overflow: hidden;
            text-align: center;
            .objectiveContainer__image {
                height: 20px;
                width: 20px;
                margin: 0 auto; }
            .objectiveContainer__number {
                height: 20px;
                font-size: 12px;
                line-height: 20px; } } } }
